const getStorage = (isPermanent: boolean) => (isPermanent ? localStorage : sessionStorage);

export const KEY_AUTH = 'AUTH_KEY';
export const KEY_AUTH_FORM = 'KEY_AUTH_FORM';
export const KEY_MEETING_ID = 'MEETING_ID';
export const KEY_MEETING_PASSWORD = 'MEETING_PASSWORD';

export function readLocallyStored<T>(
    name: string,
    defaultValue?: T,
    isPermanent = true
): T | undefined {
    if (typeof localStorage !== 'object' || typeof sessionStorage !== 'object') return defaultValue;

    let raw;
    if (typeof isPermanent === 'undefined') {
        raw = getStorage(true).getItem(name) || getStorage(false).getItem(name);
    } else {
        raw = getStorage(isPermanent).getItem(name);
    }
    return raw ? JSON.parse(raw) : defaultValue;
}

export const writeLocallyStored = (
    name: string,
    val: unknown = null,
    isPermanent = true
): void => {
    getStorage(isPermanent).setItem(name, JSON.stringify(val));
};

export const removeLocallyStored = (name: string, isPermanent = true): void => {
    getStorage(isPermanent).setItem(name, '');
};
