import type React from 'react';
import { CircularProgress, Box, Button } from '@mui/material';

export interface IMeetingLoadingProps{
    meetingId?: string;
    onReset: () => void;
}

export function MeetingLoading({meetingId, onReset}: IMeetingLoadingProps) {
    return (
        <Box sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'center',
            height: '200px',
        }}>
            <CircularProgress />
            <div style={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                marginTop: 8
            }}>
                <div style={{marginRight: 8}}>Joining meeting {meetingId ? `: ${meetingId}` : ''}</div>
                <Button variant='text' onClick={onReset}>Reset</Button>
            </div>
        </Box>
    );
}
