import type { RcvEngine } from '@ringcentral/video-sdk';
import { RcvMeetingState } from '@ringcentral/video-sdk';

export function getInitRcvFromWindow(): RcvEngine | undefined {
    const rcv = window.rcv;
    console.debug('get rcvEngine from window and got: ', rcv);
    return rcv;
}

export function checkIsInMeeting(rcvEngine?: RcvEngine): boolean {
    try {
        const res = rcvEngine?.getMeetingController()?.getMeetingState() === RcvMeetingState.MEETING_STATE_INMEETING;
        console.debug('check isInMeeting: ', res);
        return res;
    } catch (e) {
        return false;
    }
}

export function getMeetingId(rcvEngine?: RcvEngine): string | undefined {
    try {
        const res = (rcvEngine?.getMeetingController() as any)?._bridge.pins.web;
        console.debug('get meeting id from from rcvEngine and got', res);
        return res;
    } catch (e) {
        return undefined;
    }
}
