import config from '../app.config';
import { KEY_AUTH_FORM, readLocallyStored } from './local-storage';

export interface IAuth {
    authType: string
    clientId: string
    clientSecret: string
    jwt: string
    token?: any
    username?: string
    password?: string
    external?: any
    grantType?: string
}

export enum Auth_Type {
    PASSWORD= 'PASSWORD',
    TOKEN = 'TOKEN',
    JWT = 'JWT'
}

export function getDefaultAuthFormData():IAuth{
    const defaultAuthFromStorage = readLocallyStored<IAuth>(KEY_AUTH_FORM, undefined, true);
    return {
        authType: defaultAuthFromStorage?.authType || Auth_Type.JWT,
        clientId: defaultAuthFromStorage?.clientId || config?.clientId || '',
        clientSecret: defaultAuthFromStorage?.clientSecret || config?.clientSecret || '',
        token: defaultAuthFromStorage?.token || config?.token,
        username: defaultAuthFromStorage?.username || config?.username,
        password: defaultAuthFromStorage?.password || config?.password,
        jwt: defaultAuthFromStorage?.jwt || config?.jwt,
        external: defaultAuthFromStorage?.external || config?.external,
        grantType: defaultAuthFromStorage?.grantType || config?.grantType,
    };
}
