import type React from 'react';
import { useState } from 'react';
import {Paper, Button, TextField } from '@mui/material';
import type { RcvEngine} from '@ringcentral/video-sdk';
import { KEY_MEETING_ID, KEY_MEETING_PASSWORD, readLocallyStored } from '../../utils';

export interface IStartMeetingProps{
    rcvEngine: RcvEngine;
    onClear: () => void;
    onJoinMeeting: (rcvEngine: RcvEngine, meetingId?: string, password?: string) => void;
    onStartMeeting: (rcvEngine: RcvEngine) => void;
}

export function StartMeeting({ onClear, rcvEngine, onStartMeeting, onJoinMeeting }: IStartMeetingProps){
    const [meetingId, setMeetingId] = useState(readLocallyStored<string>(KEY_MEETING_ID, undefined, true));
    const [password, setPassword] = useState(readLocallyStored<string>(KEY_MEETING_PASSWORD, undefined, true));

    return (
        <>
            <Paper elevation={1} style={{ padding: 16, display: 'flex'}}>
                <TextField
                    className='item'
                    label='Meeting Id'
                    size={'small'}
                    style={{width: 200, marginRight: 4}}
                    value={meetingId}
                    variant='outlined'
                    onChange={(e) => setMeetingId(e.target.value)}
                />
                <TextField
                    className='item'
                    label='Password'
                    size={'small'}
                    style={{width: 200, marginRight: 4}}
                    value={password}
                    variant='outlined'
                    onChange={(e) => setPassword(e.target.value)}
                />
                <Button style={{marginRight: 4}} variant='contained' onClick={() => {
                    onJoinMeeting(rcvEngine, meetingId, password);
                }}>
                    Join
                </Button>
                <Button style={{marginRight: 4}} variant='outlined' onClick={() => {
                    onStartMeeting(rcvEngine);
                }}>
                    Start a Meeting
                </Button>
                <Button style={{marginRight: 4}} variant='text' onClick={onClear}>Reset</Button>
            </Paper>
        </>
    );
}
