import type React from 'react';
import { Paper, Button } from '@mui/material';
import { RcvEngineProvider } from '@provider';
import type { RcvEngine} from '@ringcentral/video-sdk';

export interface InMeetingProps{
    meetingId: string;
    rcvEngine: RcvEngine;
    onReset: () => void;
    children: React.ReactNode;
}

export function InMeeting({ onReset, children, rcvEngine, meetingId }: InMeetingProps){
    return (
        <>
            <Paper elevation={1} style={{ left: 8, top: 8, position: 'absolute', zIndex: 100, padding: 8 }}>
                <b>{meetingId}</b>
                <Button variant='text' onClick={onReset}>Reset</Button>
            </Paper>
            <RcvEngineProvider
                localeConfig={{
                    chat_dialog_title: 'My chat'
                }}
                rcvEngine={rcvEngine}
                onError={(error) => {
                  console.error('UI error happens', error);
                }}>
                {/* waitingRoomContent={<p>my waiting room</p>}> */}
                {children}
            </RcvEngineProvider>
        </>
    );
}
