import type React from 'react';
import { useState } from 'react';
import { Button, FormControl, InputLabel, MenuItem, Select, TextareaAutosize, TextField } from '@mui/material';
import type { IAuth } from '../../utils/auth';
import { Auth_Type, getDefaultAuthFormData } from '../../utils/auth';
import { KEY_AUTH_FORM, writeLocallyStored } from '../../utils';
import './index.css';

interface IAuthFormProps {
    onSubmit: (authInfo: IAuth) => void;
}

export function AuthForm({ onSubmit }: IAuthFormProps) {
    const defaultAuth = getDefaultAuthFormData();
    const [tokenInString, setTokenInString] = useState<string>(JSON.stringify(defaultAuth.token));
    const [externalInString, setExternalInString] = useState<string>(JSON.stringify(defaultAuth.external));
    const [authInfo, setAuthInfo] = useState<IAuth>(defaultAuth);

    const handleSubmit = () => {
        const toSubmitAuthInfo = {
            ...authInfo,
        };
        if (authInfo.authType === Auth_Type.TOKEN && tokenInString) {
            toSubmitAuthInfo.token = JSON.parse(tokenInString);
        }
        if (externalInString) {
            toSubmitAuthInfo.external = JSON.parse(externalInString) || {};
        }
        if (authInfo.authType === Auth_Type.PASSWORD) {
            toSubmitAuthInfo.grantType = 'PASSWORD';
        }
        writeLocallyStored(KEY_AUTH_FORM, toSubmitAuthInfo);
        onSubmit(toSubmitAuthInfo);
    };

    const renderAuthForm = () => {
        switch (authInfo.authType) {
            case Auth_Type.TOKEN:
                return <>
                    <TextField fullWidth={true} label={'clientId'} placeholder={'your clientID'} size='small' type='text' value={authInfo.clientId}
                        variant={'outlined'}
                        onChange={e => setAuthInfo({ ...authInfo, clientId: e.target.value })}
                    />
                    <div className={'br'} />
                    <TextField fullWidth={true} label='clientSecret' placeholder={'your client secret'} size='small' type='text'
                        value={authInfo.clientSecret} variant={'outlined'}
                        onChange={e => setAuthInfo({ ...authInfo, clientSecret: e.target.value })}
                    />
                    <div className={'br'} />
                    <h3>Token</h3>
                    <TextareaAutosize maxRows={20} minRows={8} placeholder={'your token'} style={{ width: '100%' }} value={tokenInString}
                        onChange={e => setTokenInString(e.target.value)}
                    />
                </>
            case Auth_Type.JWT:
                return <>
                    <TextField fullWidth={true} label={'clientId'} placeholder={'your clientID'} size='small' type='text'
                        value={authInfo.clientId}
                        variant={'outlined'}
                        onChange={e => setAuthInfo({ ...authInfo, clientId: e.target.value })}
                    />
                    <div className={'br'} />
                    <TextField fullWidth={true} label='clientSecret' placeholder={'your client secret'} size='small' type='text'
                        value={authInfo.clientSecret} variant={'outlined'}
                        onChange={e => setAuthInfo({ ...authInfo, clientSecret: e.target.value })}
                    />
                    <div className={'br'} />
                    <TextField fullWidth={true} label='JWT' placeholder={'your jwt'} size='small' type='text' value={authInfo.jwt}
                        variant={'outlined'}
                        onChange={e => setAuthInfo({ ...authInfo, jwt: e.target.value })}
                    />
                </>
            case Auth_Type.PASSWORD:
                return <>
                    <TextField fullWidth={true} label={'clientId'} placeholder={'your clientID'} size='small' type='text'
                        value={authInfo.clientId}
                        variant={'outlined'}
                        onChange={e => setAuthInfo({ ...authInfo, clientId: e.target.value })}
                    />
                    <div className={'br'} />
                    <TextField fullWidth={true} label='clientSecret' placeholder={'your client secret'} size='small' type='text'
                        value={authInfo.clientSecret} variant={'outlined'}
                        onChange={e => setAuthInfo({ ...authInfo, clientSecret: e.target.value })}
                    />
                    <div className={'br'} />
                    <TextField fullWidth={true} label='username' placeholder={'your username'} size='small' type='text' value={authInfo.username}
                        variant={'outlined'}
                        onChange={e => setAuthInfo({ ...authInfo, username: e.target.value })}
                    />
                    <div className={'br'} />
                    <TextField fullWidth={true} label='password' placeholder={'your password'} size='small' type='text' value={authInfo.password}
                        variant={'outlined'}
                        onChange={e => setAuthInfo({ ...authInfo, password: e.target.value })}
                    />
                </>
            default:
                return [];
        }

    }

    return (
        <>
            <FormControl>
                <InputLabel>Auth Type</InputLabel>
                <Select
                    label='Auth Type'
                    size='small'
                    value={authInfo.authType}
                    onChange={e => setAuthInfo({
                        ...authInfo,
                        authType: e.target.value as Auth_Type,
                    })}>
                    <MenuItem value={Auth_Type.JWT}>{Auth_Type.JWT}</MenuItem>
                    <MenuItem value={Auth_Type.PASSWORD}>{Auth_Type.PASSWORD}</MenuItem>
                    <MenuItem value={Auth_Type.TOKEN}>{Auth_Type.TOKEN}</MenuItem>
                </Select>
            </FormControl>
            <div className={'br'} />
            {renderAuthForm()}
            <h3>External config</h3>
            <TextareaAutosize
                maxRows={20}
                minRows={8}
                style={{ width: '100%' }}
                value={externalInString}
                onChange={e => setExternalInString(e.target.value)}
            />
            <div className={'br'} />
            <Button variant='contained' onClick={handleSubmit}>Init Config</Button>
        </>
    );
}
