import { KEY_AUTH, KEY_MEETING_ID, KEY_MEETING_PASSWORD, readLocallyStored, removeLocallyStored, writeLocallyStored } from '../../utils';
import type { PropsWithChildren } from 'react';
import type React from 'react';
import { useEffect, useState, useRef } from 'react';
import type { RcvEngine } from '@ringcentral/video-sdk';
import type { IAuth } from '../../utils/auth';
import { createRcvEngine } from '../../utils/create-engine';
import { StartMeeting } from '../start-meeting';
import { AuthForm } from '../auth-form';
import { checkIsInMeeting, getInitRcvFromWindow, getMeetingId } from './utils';
import { InMeeting } from '../in-meeting';
import { EngineEvent } from '@ringcentral/video-sdk';
import { MeetingLoading } from '../meeting-loading';

const MeetingWrapper: React.FC<PropsWithChildren> = ({ children }) => {
    const [rcvEngine, setRcvEngine] = useState<RcvEngine | undefined>(getInitRcvFromWindow());
    const [isJoiningMeeting, setIsJoiningMeeting] = useState<boolean>(false);
    const [inMeeting, setInMeeting] = useState<boolean>(checkIsInMeeting(rcvEngine));
    const [meetingId, setMeetingId] = useState<string | undefined>(getMeetingId(rcvEngine));
    const [auth, setAuth] = useState<IAuth | undefined>(window.auth);

    const meetingPwRef = useRef(null);

    // bind listeners
    useEffect(() => {
        if (rcvEngine) {
            console.debug(`addEventListener ${EngineEvent.MEETING_JOINED} to rcvEngine`);
            rcvEngine.on(EngineEvent.MEETING_JOINED, (meetingId) => {
                console.debug(`event ${EngineEvent.MEETING_JOINED} triggered with data: ${meetingId}`);
                setMeetingId(meetingId);
                setInMeeting(true);
                writeLocallyStored(KEY_MEETING_ID, meetingId);
                writeLocallyStored(KEY_MEETING_PASSWORD, meetingPwRef.current);
            });
            console.debug(`addEventListener ${EngineEvent.MEETING_JOINED} to rcvEngine`);
            rcvEngine.on(EngineEvent.MEETING_LEFT, () => {
                console.debug(`event ${EngineEvent.MEETING_LEFT} triggered`);
                setMeetingId('');
                setInMeeting(false);
                writeLocallyStored(KEY_MEETING_ID, '');
                writeLocallyStored(KEY_MEETING_PASSWORD, '');
            });
        }
    }, [rcvEngine]);

    // rejoin a meeting on refresh
    useEffect(() => {
        const rejoinMeeting = async () => {
            if (window.rcv) {
                console.debug('got rcvEngine from window, no need to rejoin a meeting');
                return;
            }
            const defaultAuthFromStorage = readLocallyStored<IAuth>(KEY_AUTH, undefined, true);
            if (defaultAuthFromStorage) {
                console.debug('got auth info from local storage, will init rcvEngine automatically');
                await handleSubmitAuth(defaultAuthFromStorage);
                const rcv = await handleInitRcv(defaultAuthFromStorage);
                const meetingId = readLocallyStored<string>(KEY_MEETING_ID, undefined, true);
                const password = readLocallyStored<string>(KEY_MEETING_PASSWORD, undefined, true);
                if (meetingId) {
                    console.debug(`got meetingId from local storage, will rejoin meeting ${meetingId}`);
                    await handleJoinMeeting(rcv, meetingId, password);
                }
            }
        };
        rejoinMeeting().then();
    }, []);

    const handleStartMeeting = async (rcvEngine: RcvEngine) => {
        setIsJoiningMeeting(true);
        console.debug('start meeting');
        await rcvEngine.startInstantMeeting();
        setIsJoiningMeeting(false);
    };

    const handleJoinMeeting = async (rcvEngine: RcvEngine, meetingId?: string, password?: string) => {
        if (!meetingId) {
            alert('Meeting id can not be empty!');
            return;
        }
        setMeetingId(meetingId);
        setIsJoiningMeeting(true);
        console.debug(`join meeting with meetingId: ${meetingId}`);
        meetingPwRef.current = password;
        await rcvEngine.joinMeeting(meetingId, password ? { password }: {});
        setIsJoiningMeeting(false);
    };

    const handleSubmitAuth = async (authInfo: IAuth): Promise<IAuth> => {
        window.auth = authInfo;
        setAuth(authInfo);
        writeLocallyStored(KEY_AUTH, authInfo, true);
        return authInfo;
    };

    const handleInitRcv = async (authInfo: IAuth): Promise<RcvEngine> => {
        const rcv = await createRcvEngine({
            ...authInfo,
        });
        window.rcv = rcv;
        setRcvEngine(rcv);
        return rcv;
    };

    const handleReset = () => {
        removeLocallyStored(KEY_AUTH);
        removeLocallyStored(KEY_MEETING_ID);
        rcvEngine?.destroy();
        setRcvEngine(undefined);
        setAuth(undefined);
        window.rcv = undefined;
        window.auth = undefined;
    };

    if (isJoiningMeeting) {
        return <MeetingLoading meetingId={meetingId} onReset={handleReset} />;
    }
    if (rcvEngine) {
        if (inMeeting && meetingId) {
            return <InMeeting meetingId={meetingId} rcvEngine={rcvEngine} onReset={handleReset}>{children}</InMeeting>;
        } else if (!inMeeting) {
            return <StartMeeting rcvEngine={rcvEngine} onClear={handleReset} onJoinMeeting={handleJoinMeeting}
                onStartMeeting={handleStartMeeting}
                   />;
        }
    }
    if (!auth) {
        return (
            <div style={{padding: '1em'}}>
                <AuthForm
                    onSubmit={async (authInfo) => {
                        await handleSubmitAuth(authInfo);
                        await handleInitRcv(authInfo);
                    }}
                />
            </div>
        );
    }
    return <p>loading...</p>;
};

export default MeetingWrapper;
