import { GrantType, RcvEngine } from '@ringcentral/video-sdk';
import config from '../app.config';
import type { IAuth } from './auth';
import { Auth_Type } from './auth';

export async function createRcvEngine(auth: IAuth) {
    RcvEngine.instance()?.destroy();
    const rcv = RcvEngine.create({
        enableDiscovery: false,
        origin: config.origin || 'https://platform.ringcentral.com',
        ...auth,
        external: {
            ...auth.external,
            ...config.dev
        },
    });
    if (auth.authType === Auth_Type.TOKEN) {
        await rcv.setAuthToken(JSON.stringify(auth.token));
    } else if (auth.authType === Auth_Type.JWT) {
        await rcv.authorize({
            grantType: GrantType.JWT,
            jwt: auth.jwt,
        });
    } else {
        await rcv.authorize({
            grantType: GrantType.PASSWORD,
            username: auth.username,
            password: auth.password,
        });
    }
    console.debug('createRcvEngine called and got: ', rcv);
    return rcv;
}
